.view-bookings-container {
  max-width: 700px;
  margin: 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.view-booking-heading {
  font-size: 1rem;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #7c8288;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  width: 99%;
  margin-top: 0px;

}

.view-book-label {
  width: 100%;
  padding: 6px;
  margin: 0;
  background-color: #d6dadd;
  color: #2e2e2f;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 25px;
  margin-bottom: 10px;
  border-top: 2px solid #851212;
}

.calendar {
  display: flex;
  flex-wrap: wrap;
}

.calendar-nav {
  margin-bottom: 10px;
}

.calendar-grid {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.calendar-day {
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.calendar-day h3 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  font-size: 15px;
  border-radius: 50%;
  padding: 1px;
  background-color: #ccd0d7;
}

.booking-section {
  margin-bottom: 20px;
}

.booking-section h4 {
  display: flex;
  margin-bottom: 10px;
  font-size: 18px;
  color: #333;
  border-top: 2px solid #851212;
}

.booking-table-container {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.booking-table {
  width: 100%;
  padding: 1px;
  border-collapse: collapse;
}

.booking-table td,
.booking-table th {
  padding: 8px;
  border: 1px solid #ddd;
}

.booking-table th {
  border-top: 2px solid #851212;
}

.booking-table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

.booking-table tbody tr:hover {
  background-color: #ddd;
}

.booking-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #babfc2;
  color: #2e2e2f;
}

.mbutton {
  padding: 10px 20px;
  background-color: #3c90ea;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 20px;
  margin-bottom: 10px;
}

.mbutton:hover {
  background-color: #0056b3;
}
