/* App container */
.app-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Header */
h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

/* Navigation */
.navbar {
  margin-bottom: 20px;
}

.navbar button {
  padding: 10px 20px;
  margin-right: 10px;
  font-size: 16px;
  background-color: #4f4f52;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.navbar button:hover {
  background-color: #667382;
}

/* Style for active tab button */
.navbar button.active {
  background-color: #7c8288; /* Change color for active tab */
}

.navbar button.active:hover {
  background-color: #45515d;
}

/* PIN input container */
.pin-input-container {
  margin-bottom: 20px;
}

.pin-input-container input[type="password"] {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.pin-input-container button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.pin-input-container button:hover {
  background-color: #218838;
}


