/* AdminMenu.css */

.admin-menu-container {
  max-width: 700px;
  margin: 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.admin-menu-heading {
  font-size: 1rem;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #7c8288;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  margin-top: 0px;


}

.admin-menu-section {
  margin-bottom: 15px;
}

.admin-menu-label {
  display: block;
  margin-bottom: 0px;
}

.admin-menu-input,
.admin-menu-select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.admin-menu-button,
.admin-menu-submit-button {
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.admin-menu-button:hover,
.admin-menu-submit-button:hover {
  background-color: #0056b3;
}

.admin-menu-checkbox-list {
  list-style: none;
  padding: 0;
}

.admin-menu-checkbox-item {
  margin-bottom: 10px;
}

.admin-menu-checkbox-label {
  display: inline-block;
  margin-left: 5px;
}

.admin-menu-checkbox {
  margin-right: 5px;
}

/* Styles for the admin menu list */
.admin-menu-list {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.admin-menu-list th,
.admin-menu-list td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.admin-menu-list th {
  background-color: #f2f2f2;
}

.admin-menu-list tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.admin-menu-subheading {
  display: inline-block;
  width: 98%;
  /* Fixed width */
  padding: 6px;
  margin: 0;
  background-color: #d6dadd;
  color: #434141;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 0.95rem;
  border-top: 2px solid #851212;

}



.admin-menu-subheading:hover {
  background-color: #a2bbd6;
}

/* Styles for delete button */
.admin-menu-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* Align buttons to start of the column */
}


.delete-button {
  background-color: rgb(203, 59, 59);
  color: white;
  border: none;
  padding: 3px 6px;
  cursor: pointer;
  text-align: center;
  /* Center the text */
  margin-left: 4px;
  margin-bottom: 5px;
}



/* Styles for Select Food */


.admin-menu-category:hover {
  background-color: #0056b3;
  /* Adjusted hover background color */
}

.foodprice-input {
  width: 25%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 0px;
  margin-right: 10px;
}

.foodprice-input::-webkit-outer-spin-button,
.foodprice-input::-webkit-inner-spin-button {
  -webkit-appearance: none;

}

.foodname-input {
  width: 25%;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: -8px;
  margin-right: 10px;
}


.time {
  width: 25%; /* Adjust width as needed */
  padding: 6px; /* Adjust padding as needed */
  border: 1px solid #ccc; /* Border color */
  border-radius: 4px; /* Border radius */
  margin-top: 0px; /* Adjust margin top as needed */
  margin-right: 10px;
  box-sizing: border-box; /* Include padding and border in width */
}

.select-package {
  width: 100%;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.admin-menuButton {
  padding: 8px 16px; /* Adjust padding as needed */
  background-color: #3c90ea; /* Background color */
  color: #fff; /* Text color */
  border: 1px solid #6990ba; /* Border color */
  border-radius: 4px; /* Border radius */
  cursor: pointer; /* Show pointer cursor on hover */
  text-align: center; /* Center text horizontally */
  text-decoration: none; /* Remove default underline */
  display: inline-block; /* Ensure block-level behavior */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow */
  margin-bottom: 10px;
}

.admin-menuButton:hover {
  background-color: #0056b3; /* Change background color on hover */
}

.admin-saveButton {
  padding: 6px 12px; /* Adjust padding as needed */
  background-color: #3c90ea; /* Background color */
  color: #fff; /* Text color */
  border: 1px solid #6990ba; /* Border color */
  border-radius: 4px; /* Border radius */
  cursor: pointer; /* Show pointer cursor on hover */
  text-align: center; /* Center text horizontally */
  text-decoration: none; /* Remove default underline */
  display: inline-block; /* Ensure block-level behavior */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow */
  margin-top:10px;
}

.admin-saveButton:hover {
  background-color: #0056b3; /* Change background color on hover */
}

.admin-staffButton {
  padding: 6px 12px; /* Adjust padding as needed */
  background-color: #3c90ea; /* Background color */
  color: #fff; /* Text color */
  border: 1px solid #6990ba; /* Border color */
  border-radius: 4px; /* Border radius */
  cursor: pointer; /* Show pointer cursor on hover */
  text-align: center; /* Center text horizontally */
  text-decoration: none; /* Remove default underline */
  display: inline-block; /* Ensure block-level behavior */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow */
  margin-top:10px;
  margin-left: 10px;
}

.admin-staffButton:hover {
  background-color: #0056b3; /* Change background color on hover */
}
