/* NewBooking.css */

.booking-container {
    max-width: 700px;
    margin: 0 ;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

  }
  
 
  
  .customer-section,
  .hall-section,
  .date-time-section,
  .package-section,
  .package-details,
  .custom-package,
  .food-section,
  .soft-drink-section,
  .hard-drink-section {
    margin-bottom: 20px;
  }
  
  .labelhead {
    display: flex;
    justify-content: center; /* Center-align horizontally */
    align-items: center; /* Center-align vertically */
    font-size: 1rem;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #7c8288;
    color: #fff;
    border-radius: 4px;
    font-weight: bold; /* Make text bold */
  }
  

  
  .label {
    display: flex;
    padding: 6px;
    margin: 0;
    background-color: #d6dadd;
    color: #222121;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 25px;
    margin-bottom: 20px;
    font-weight: bold; /* Make text bold */
  
    /* Add borders */
    border-top: 2px solid #851212;
  }
  
  
  .input,
  .select {
    width: 97%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: -8px;
  }
  
  .button,
  .submit-button {
    padding: 7px 14px;
    background-color: #3c90ea;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
    margin-top: 4px;
  }
  
  .button:hover,
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .checkbox-list {
    list-style: none;
    padding: 0;
  }
  
  .checkbox-item {
    margin-bottom: 10px;
  }
  
  .checkbox-label {
    display: inline-block;
    margin-left: 5px;
  }
  
  .checkbox {
    margin-right: 5px;
  }

  
  /* Styles for the customer table */
.customer-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.customer-table th,
.customer-table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.customer-table th {
  background-color: #f2f2f2;
}

.customer-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.checkbox-calculate{
  display: inline-block;
}
.checkbox-container{
  background-color: #d6dadd;
  padding: 5px;
  margin-top: 15px;
  margin-bottom: 15px;
  border-top: 2px solid #851212;
border-radius: 4px;
font-weight:510;
}

.total-cost-section {
  border: 2px solid #7c8288;
  padding: 10px;
  margin-top: 20px;
  background-color: #c5c0c0;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}

.price-calculation-container {
  display: flex;
  align-items: center;
}

/* Adjust margin or padding as needed */
.calculate-button{
  padding: 4px 8px;
  background-color: #3c90ea;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
}

.calculate-button:hover {
  background-color: #0056b3;
}

.price-per-person-section {
  margin-right: 10px; /* Add margin between the button and the input field */
}

.number-input{
  width: 97%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: -8px;
}

.number-input::-webkit-outer-spin-button,
.number-input::-webkit-inner-spin-button {
  -webkit-appearance: none;

}

.foodname-input {
  width: 25%;
  padding: 8px; /* Adjust padding as needed */
  border: 1px solid #ccc; /* Border color */
  border-radius: 4px; /* Border radius */
  margin-top: 8px; /* Adjust margin top as needed */
}
