.math-container {
  font-family: Arial, sans-serif;
  margin: 0;
  max-width: 700px;
  margin: 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.math-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.math-table th,
.math-table td {
  padding: 8px;
  border: 1px solid #ddd;
}

.math-table th {
  background-color: #f2f2f2;
}

.math-table tr:last-child {
  font-weight: bold;
  background-color: #ffc107;
}

.math-heading {
  font-size: 1rem;
  margin-top: 0px;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #7c8288;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  width: 97%;

}

.year-label {
  width: 98%;
  padding: 6px;
  margin: 0;
  background-color: #d6dadd;
  color: #2e2e2f;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 25px;
  margin-bottom: 10px;
  font-weight: bold;
  /* Make text bold */

  /* Add borders */
  border-top: 2px solid #851212;
}

.select-year {
  width: 25%;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 0px;
}