/* components/ViewCustomers.css */
.view-customers {
  max-width: 700px;
  margin: 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .view-customer-heading {
    font-size: 1rem;
    margin-bottom: 25px;
    padding: 10px;
    background-color: #7c8288;
    color: #fff;
    border-radius: 4px; 
    text-align: center;
    width: 98%;
  margin-top: 0px;
  }
  
  
  .view-customers table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .view-customers th {
    border: 1px solid #bdbbbb;
    padding: 8px;
    text-align: left;
    border-top: 2px solid #851212;
  }
   
  .view-customers td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .view-customers th {
    background-color: #d6dadd;
  }
  
  .view-customers tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .view-customers tr:hover {
    background-color: #ddd;
  }
  